<template>
  <div id="app">
    <Header>
      <div class="container">
        <div id="nav">
          <div class="flexItem"  @click="changeTab">
            <!--        <img class="btnImg" src="../assets/img/navBtn.png" />-->
            <Icon class="close" :class="classClose" type="md-add" size="30" color="white"/>
            <Icon class="menu" :class="classMenu" type="md-menu" size="30" color="white"/>
          </div>
          <div class="nav-box" :class="classClose" @click="changeTab">
            <div class="nav-content">
              <router-link to="/"><i class="iconfont icon-shouye"></i>首页</router-link>
              <router-link to="/tips"><i class="iconfont icon-biaoqian"></i>标签</router-link>
              <router-link to="/category"><i class="iconfont icon-fenlei"></i>分类</router-link>
              <router-link to="/file"><i class="iconfont icon-guidang"></i>归档</router-link>
              <router-link to="/life"><i class="iconfont icon-icon-view"></i>行博</router-link>
              <router-link to="/about"><i class="iconfont icon-guanyu"></i>关于</router-link>
            </div>
            <div class="author-info">
              <h4>关于作者</h4>
              <p class="name">Yang Xin Ming</p>
              <p class="desc">广东工业大学计算机学院软件工程学生</p>
              <p>
                <a href="https://github.com/seasonin"><span class="iconfont icon-github">&nbsp;GitHub</span></a>
                <span>&nbsp;&nbsp;&nbsp;|&nbsp;&nbsp;&nbsp;</span>
                <a href="mailto:xmyang1@126.com"><span class="iconfont icon-email">&nbsp;Email</span></a>
              </p>
            </div>
          </div>
        </div>
      </div>
    </Header>
    <router-view/>
    <footer class="footer">
      <a href="http://www.beian.miit.gov.cn/" target="_blank">粤ICP备19114699号</a>
    </footer>
  </div>
</template>

<script>
  // @ is an alias to /src
  // import HelloWorld from '@/components/HelloWorld.vue'
  // import blogHead from '@/components/blogHead'
  // import header from '@/components/header/header'
  //
  export default {
    data () {
      return {
        toggleTab: false,
        classClose: 'hide',
        classMenu: 'show'
      }
    },
    methods: {
      changeTab: function () {
        if (this.toggleTab) {
          this.classClose = 'hide',
          this.classMenu = 'show'
        } else {
            this.classClose = 'show',
            this.classMenu = 'hide'
        }
        this.toggleTab = !this.toggleTab
        // console.log(itemTab.style.height)
      }
    }
  }
</script>

<style lang="scss">
  @import './assets/css/common';

  #app {
    padding-top: 64px;

    @media screen and (max-width: 767px) {
      padding-top: 48px;
    }

    .ivu-layout-header {
      position: fixed;
      background: #fff;
      width: 100%;
      top: 0;
      /*padding: 20px 0;*/
      z-index: 1000;

      .container {
        #nav {
          .flexItem {
            display: none;

            .hide {
              display: none;
            }

            .show {
              display: block;
            }

            .close {
              color: #000 !important;
              width: max-content;
              transform: rotate(45deg);
              transition: all ease .5s;
            }

            .menu {
              color: #000 !important;
              width: max-content;
              transition: all ease .5s;
            }
          }

          .nav-box {
            display: block;

            .nav-content {
              a {
                text-align: center;
                position: relative;
                display: inline-block;
                height: 100%;
                color: inherit;
                padding: 0 25px;
                font-size: 20px;

                i {
                  font-size: 20px;
                  margin-right: 5px;
                }

                &:after {
                  display: block;
                  content: '';
                  position: absolute;
                  bottom: 0;
                  left: 0;
                  width: 100%;
                  transform: scale(0);
                  transition: all ease-in 0.5s;
                  height: 4px;
                  border-radius: 2px;
                  background: $linkColor;
                }

                &.router-link-exact-active {
                  &:after {
                    transform: scale(1);
                  }
                }

                @media screen and (min-width: 768px) {
                  &:hover {
                    &:after {
                      transform: scale(1);
                    }
                  }
                }
              }
            }

            .author-info {
              display: none;
            }
          }
        }
      }

      @media screen and (max-width: 767px) {
        padding: 0;
        height: 48px;
        line-height: 48px;
        display: table;

        .container {
          display: table-cell;
          vertical-align: middle;

          #nav {
            .flexItem {
              display: block;
              position: relative;
            }

            .nav-box {
              padding: 0 15px;
              overflow-y: scroll;
              width: 100%;
              transition: height .5s;
              position: fixed;
              left: 0;
              height: 100%;
              background: #fff;

              &.hide {
                display: none;
              }

              &.show {
                display: block;
              }

              .nav-content {
                a {
                  display: block;
                  margin: 10px 0;
                  padding: 0;
                  text-align: left;

                  &:after {
                    transform: scale(1);
                    height: 1px;
                  }

                  &:first-child {
                    margin-top: 20px;
                  }
                }
              }

              .author-info {
                display: block;
                margin-top: 20px;

                p {
                  margin: 10px 0;

                  a {
                    color: inherit;
                  }

                  span {
                    font-size: inherit;
                  }
                }
              }
            }
          }
        }
      }
    }

    .footer {
      padding: 80px 0 10px;
      background: rgb(237, 237, 239);
      text-align: center;

      a {
        color: inherit;
      }

      @media screen and (max-width: 767px) {
        padding: 30px 0 10px;
      }
    }
  }
</style>
